import React from 'react'
import { Link, graphql } from 'gatsby'
import Layout from '../components/layout' 
import ReactMarkdown from 'react-markdown'

export default class ArticleTemplate extends React.Component{
    constructor(props){
        super(props);

        this.state = {
            items: [],
            isLoaded: false,
            error: null
        }
    }

    componentDidMount() {

        let header = new Headers({
            'Access-Control-Allow-Origin':'*'
        });
        let sentData={
            method: "GET",
            mode: 'cors',
            header: header
        };

        fetch("https://swapi.co/api/people", sentData)
          .then(res => res.json())
          .then(
            (result) => {
                debugger
                this.setState({
                    items: result.results,
                    isLoaded: true,
                });
            },
            // Note: it's important to handle errors here
            // instead of a catch() block so that we don't swallow
            // exceptions from actual bugs in components.
            (error) => {
                debugger
                this.setState({
                    isLoaded: true,
                    error
                  });
            }
        )
    }

    render(){
        let data = this.props.data;
        const { error, isLoaded, items } = this.state;

        let dynamicContent = "";
            
        if(error) {
            dynamicContent = ( <div>Error: {error.message}</div> );
        } else if(!isLoaded){
            dynamicContent = ( <div>Loading...</div> );
        } else {
            dynamicContent = (
                <ul>
                {items.map(item => (
                  <li key={item.name}>
                    {item.name} {item.price}
                  </li>
                ))}
              </ul>
            )
        }

        return (
        <Layout>
            <h1>{data.strapiArticle.title}</h1>
           
            <p>by <Link to={`/authors/Author_${data.strapiArticle.author.id}`}>{data.strapiArticle.author.username}</Link></p>
            <i>This content comes from Strapi</i>
            
            <ReactMarkdown source = {data.strapiArticle.content}></ReactMarkdown> <br></br>

            <div>
                <h3>Dynamic content: A list of Star Wars heroes from https://swapi.co/api/people</h3>
                { dynamicContent }
            </div>
        </Layout>
        )
    }
}


export const query = graphql`
  query ArticleTemplate($id: String!) {
    strapiArticle(id: {eq: $id}) {
      title
      content
      author {
        id
        username
      }
    }
  }
`