import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"

const Header = ({ siteTitle }) => (
  <header
    style={{
      background: `rebeccapurple`,
      marginBottom: `1.45rem`,
    }}
  >
    <div
      style={{
        margin: `0 auto`,
        maxWidth: 960,
        padding: `1.45rem 1.0875rem`,
      }}
    >
      <ul>
        <li style = {{
          display: "inline"
        }}>
          <Link
            to="/"
            style={{
              color: `white`,
              textDecoration: `none`,
            }}
          >
            {siteTitle}
          </Link>
        </li>


        <li style = {{
          display: "inline",
          margin: 10
        }}>
          <Link to="/app/dashboard"        style={{
              color: `white`,
              textDecoration: `none`,
            }}>Dashboard</Link>
        </li>

        <li style = {{
          display: "inline",
          margin: 10
        }}>
          <Link to="/app/offers"        style={{
              color: `white`,
              textDecoration: `none`,
            }}>Offers</Link>
        </li>


      </ul>
    </div>
  </header>
)

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
